<script>

    import { moveSocrller } from '../../assets/JS/common.js'
    export default {
      name: 'our',
      data: function(){
        return {
          markerArr:[{title:"",content:"3",point:"104.035421|30.614727",isOpen:0,icon:{w:21,h:21,l:0,t:0,x:6,lb:5}}] /*//*/
        }
      },
      mounted: function(){
        this.initMap();
      },
      created() {
        window.scrollTo(0,0);
      },
      methods: {
        // 
        KnowUs:function(){moveSocrller($(".KnowUs").offset().top-120)},
        adress:function(){moveSocrller($(".map").offset().top-120)},
        contact:function(){moveSocrller($(".ourContainer").offset().top-120)},


        //
        initMap: function(){
          this.createMap();//
          this.setMapEvent();//
          this.addMapControl();//
          this.addMarker();//marker
        },
        createMap: function(){
          var map = new BMap.Map("dituContent");//
          var point = new BMap.Point(104.035879,30.620212);//
          map.centerAndZoom(point,15);//
          window.map = map;//map
        },
        setMapEvent: function(){
          map.enableDragging();//()
          map.enableScrollWheelZoom();//
          map.enableDoubleClickZoom();//()
          map.enableKeyboard();//
        },
       addMapControl: function(){
          //
          var ctrl_nav = new BMap.NavigationControl({anchor:BMAP_ANCHOR_TOP_LEFT,type:BMAP_NAVIGATION_CONTROL_LARGE});
          map.addControl(ctrl_nav);
          //
          var ctrl_ove = new BMap.OverviewMapControl({anchor:BMAP_ANCHOR_BOTTOM_RIGHT,isOpen:1});
          map.addControl(ctrl_ove);
          //
          var ctrl_sca = new BMap.ScaleControl({anchor:BMAP_ANCHOR_BOTTOM_LEFT});
          map.addControl(ctrl_sca);
        },
        //marker
        addMarker:function (){
          var markerArr = [{title:"",content:"3",point:"104.035421|30.614727",isOpen:0,icon:{w:21,h:21,l:0,t:0,x:6,lb:5}}]
            for(var i=0;i<markerArr.length;i++){
              var json = markerArr[i];
              var p0 = json.point.split("|")[0];
              var p1 = json.point.split("|")[1];
              var point = new BMap.Point(p0,p1);
              var iconImg = this.createIcon(json.icon);
              var marker = new BMap.Marker(point,{icon:iconImg});
              var iw = this.createInfoWindow(i);
              var label = new BMap.Label(json.title,{"offset":new BMap.Size(json.icon.lb-json.icon.x+10,-20)});
              marker.setLabel(label);
              map.addOverlay(marker);
              label.setStyle({
                borderColor:"#808080",
                color:"#333",
                cursor:"pointer"
              });

//              (function(){
                var index = i;
                var _iw = this.createInfoWindow(i);
                var _marker = marker;
                _marker.addEventListener("click",function(){
                  this.openInfoWindow(_iw);
                });
                _iw.addEventListener("open",function(){
                  _marker.getLabel().hide();
                })
                _iw.addEventListener("close",function(){
                  _marker.getLabel().show();
                })
                label.addEventListener("click",function(){
                  _marker.openInfoWindow(_iw);
                })
                if(!!json.isOpen){
                  label.hide();
                  _marker.openInfoWindow(_iw);
                }
//              })()
            }
          },
        //InfoWindow
        createInfoWindow:function (i){
            var json = this.markerArr[i];
            var iw = new BMap.InfoWindow("<b class='iw_poi_title' title='" + json.title + "'>" + json.title + "</b><div class='iw_poi_content'>"+json.content+"</div>");
            return iw;
          },
//          Icon
           createIcon:function(json){
            var icon = new BMap.Icon("../../assets/img/logo-2x.png", new BMap.Size(json.w,json.h),{imageOffset: new BMap.Size(-json.l,-json.t),infoWindowOffset:new BMap.Size(json.lb+5,1),offset:new BMap.Size(json.x,json.h)})
            return icon;
          }

      }
    }
</script>
<style type="text/css">
        html,body{margin:0;padding:0;}
        .iw_poi_title {color:#CC5522;font-size:14px;font-weight:bold;overflow:hidden;padding-right:13px;white-space:nowrap}
        .iw_poi_content {font:12px arial,sans-serif;overflow:visible;padding-top:4px;white-space:-moz-pre-wrap;word-wrap:break-word}
    </style>
<style scoped src="../../assets/css/our.css"></style>



