<template>

  <!--  -->
  <div class="dataC" v-if='isShow'>
    <div>{{msg.min}}</div>
    <!--  ECharts  DOM -->
    <div id="week" class="dataCavas" style="width: 100%;height:300px;"></div>
    <div hidden>{{msg.params.x}}</div>
  </div>

</template>
<script>
import { setGraphOpt } from '../../../assets/JS/common.js'
  export default {
    props:{
      'msg':{
        type:Object
      },
        'isShow':{
          type:Boolean
        }
    },
    data: function(){
      return {

      }
    },
    updated: function(){
      this.drawing()
      // console.log('this.msg.params.x=',this.msg.params.x)
      // console.log('this.msg.params.y=',this.msg.params.y)
    },
    mounted: function(){
       this.drawing()
    },
    methods: {
      drawing: function(){
        if(this.isShow == true){
            var week = this.$echarts.init(document.getElementById('week'));
    // 
           var x =this.msg.params.x
           var y =  this.msg.params.y
           var option_week = setGraphOpt('','y/m',x,y)
    // 
          week.setOption(option_week);
          window.addEventListener("resize",function(){
            week.resize();
          });
        }
        
      }
    }
  }
</script>
<style scoped src=''>

  .dataC{
    background: white;
  }
</style>



