<template>
	<div class="edtorCon">
		<p class="text-center"></p>
		<form>
			<p>{{msg.identifier}}</p>
			<p><label for="nickName">&emsp;&emsp;</label><input type="text" name="nickName" id="nickName" v-model="msg.nickName"></p>
			<p><label for="heightUse"> /cm</label><input type="Number" name="heightUse" id="heightUse" v-model="msg.stature"></p>
			<p><label for="weight"> /kg</label><input type="Number" name="weight" id="weight" v-model="msg.weight"></p>
			<!-- <p><label for="birthday">&emsp;&emsp;</label><input type="text" name="birthday" id="birthday"></p> -->
			<p><label for="status"></label>
				<select v-model="msg.status" >
				    <option v-for="item in status.statusGrup" :value="item">
				    	<span v-if="item == 0"></span>
				    	<span v-if="item == 1"></span>
				    </option>
				</select>
				<!-- <input type="text" name="status" id="status" v-model="msg.status"> --></p>
			<p><label for="psw">&emsp;&emsp;</label><input type="password" name="psw" id="psw" v-model="msg.psw" disabled="pswDisablede" ><span @click='showRePswF()'></span></p>
			<p v-show="msg.repswShow"><label for="repsw"></label><input type="password" name="repsw" id="repsw" disabled="pswDisablede" v-model="msg.repsw"></p>
			<p><label for="qx"></label><!-- <input type="text" name="qx" id="qx" v-model="msg.role"> -->
				<select v-model="msg.role" >
				    <option v-for="item in role.roleGrup" :value="item">
				    	<span v-if="item == 10"></span>
				    	<span v-if="item == 100"></span>
				    	<span v-if="item == 1000"></span>
				    	<span v-if="item == 10000"></span>
				    </option>
				</select>
			</p>
			<p class="text-center Prompt">{{msg.Prompt}}</p>
			<p class="text-center" ><button type="button" @click="sumbitF"></button></p>
		</form>

		<!--  -->
		<!-- <p class="text-center" v-show='false'></p>
		<div class="olderGorup text-center" v-show='false'>
			<div class="overflower">
				<div class="floatL"></div>
				<div class="floatL">68</div>
				<div class="floatL"></div>
			</div>
			<div>
				<div>
					<span></span>
					<span><input type="radio" name="blood"  checked="true" value="1"></span>
					<span><input type="radio" name="blood" checked="false" value='0'></span>
				</div>
				<div>
					<span></span>
					<span><input type="radio" name="bloodItch" value="1"></span>
					<span><input type="radio" name="bloodItch" value='0'></span>
				</div>
				<div>
					<span></span>
					<span><input type="radio" name="heart" value="1"></span>
					<span><input type="radio" name="heart" value='0'></span>
				</div>
			</div>
			<div><button></button></div>
		</div> -->


	</div>

	
</template>
<script>
    export default {
    	'name': 'useEditor',
    	props:{
    		"msg":{
	    		type:[Object,Array]
	    	},
	    	'isShow':Boolean
    	},
    	
    	data: function(){
    		return {
    			pswDisablede:false,
    			status:{
    				statusGrup:[0,1]
    			},
    			role:{
    				roleGrup:[10,100,1000,10000]    // 10:  100  1000   10000:
    			},
    			formMsg:{},
    			useEditMsg:{},
    			testStature:/^[1-3]{1}[0-9]{2}$/,   //
    			testWight:/^[1-9]{1}[0-9]{1,2}$/,   //
    			tesPsw:/^[0-9]{1,6}$/,              //
    			resMsg:""
    		}
    	},
    	updated:function(){    //
    		if(this.msg.repswShow == false){
    			$("#psw").attr("disabled","disabled");
    			$("#repsw").attr("disabled","disabled");
    		}
    	},
    	mounted:function(){
    		this.useEditMsg.token = window.localStorage.getItem("token");
    	},
    	methods: {
    		showRePswF: function(){
    			this.msg.repswShow = true
    			$("#psw").removeAttr("disabled");
    			$("#repsw").removeAttr("disabled");
    			
    		},
    		selectChange: function(){
    			// this.msg.status = this.status.selcted;
    		},
    		sumbitF: function(){
    			if(this.msg.repswShow == true){
    				if(this.msg.nickName == '' ){
	    				this.msg.Prompt = ""
	    			}else if(!this.testStature.test(Number(this.msg.stature))){
	    				this.msg.Prompt = "182"
	    			}else if(!this.testWight.test(Number(this.msg.weight))){
	    				this.msg.Prompt = "80"
	    			}else if(this.msg.psw == '' || !this.tesPsw.test(Number(this.msg.psw))){
	    				this.msg.Prompt = "1-6 "
	    			}else if(this.msg.psw != this.msg.repsw){
	    				this.msg.Prompt = ""
	    			}else {
	    				this.msg.Prompt = '';
	    				this.formMsg = {
	    					'identifier':this.msg.identifier,
		    				'nickName':this.msg.nickName,
		    				'stature':this.msg.stature,
		    				'weight':this.msg.weight,
		    				'role':this.msg.role,    //
		    				'psw':this.getpsw,
		    				'status':this.msg.status
		    			}
		    			this.useEditMsg.formMsg = JSON.stringify(this.formMsg);
		    			this.postUrlF(); //
	    			}
    			}else if(this.msg.repswShow == false){
    				if(this.msg.nickName == '' ){
	    				this.msg.Prompt = ""
	    			}else if(!this.testStature.test(Number(this.msg.stature))){
	    				this.msg.Prompt = "182"
	    			}else if(!this.testWight.test(Number(this.msg.weight))){
	    				this.msg.Prompt = "80"
	    			}else {
	    				this.msg.Prompt = '';

	    				this.formMsg = {
	    					'identifier':this.msg.identifier,
		    				'nickName':this.msg.nickName,
		    				'stature':this.msg.stature,
		    				'weight':this.msg.weight,
		    				'role':this.msg.role,    //
		    				'psw':this.getpsw,
		    				'status':this.msg.status
		    			}
		    			this.useEditMsg.formMsg = JSON.stringify(this.formMsg);
		    			this.postUrlF(); //
	    			}
    			}
    		},
    		postUrlF: function(){   //
    			// console.log(this.formMsg,"formMsg")
    			this.$emit("form-btn-click",this.useEditMsg);
    		}
    	},
    	computed: {
    		getpsw: function(){
    			return this.msg.psw
    		},
    		getrepsw: function(){
    			return this.msg.repsw
    		},
    		
    	}
    }
</script>
<style scoped>
.edtorCon>p{
	margin-bottom: 50px;
	font-weight: bold;
}
form>p{
	height: auto;
	margin-top: 10px;

}
form>p>input,form>p>select{
	height: 25px;
	padding-left: 10px;
	border:1px solid #495B69;
	outline: none;
}
form>p>select{
	    width: 273px;
    margin-left: -5px;
}
form>p>span{
	display: inline-block;
	padding:5px;
	background: #EE3D6E;
	font-size: 12px;
	color: white;
	margin-left: 5px;
	border-radius: 5px;
}
form>p>span:hover{
	cursor: pointer;
}
button{
 		width: 88px;
 		height: 25px;
 		line-height: 25px;
 		font-size: 13px;
 		color: white;
 		background: #EE3D6E;
 		border-radius: 5px;
 		border:none;
 		margin-top: 20px;
}
.Prompt{
	font-size: 14px;
	color: #EE3D6E;
}

/*css*/
.olderGorup>div:nth-of-type(1){
	/*padding-bottom: 20px;*/
}
.olderGorup>div:nth-of-type(1)>div{
	margin-left: 30px;
}
.olderGorup>div:nth-of-type(1)>div:nth-of-type(1){
	margin-left: 0;
}
.olderGorup>div:nth-of-type(2){
	/*border: 1px solid red;*/
	margin: 0 auto;
	width: auto;
}
.olderGorup>div:nth-of-type(2)>div{
	margin-top: 25px;
}
.olderGorup>div:nth-of-type(2)>div>span:nth-of-type(3){
	margin-left: 20px;
	display: inline-block;
}
.olderGorup>div:nth-of-type(3){
	margin-top: 30px;
}
</style>



