<template>
  <header>
    <section class="logo"><router-link :to="{name:'beforwebHome'}"><!--<img class="img-responsive" src="../../../assets/img/logo-2x.png">--></router-link></section>
    <nav>
      <!-- <a id="product" href="#" @click="product"></a> -->
      <router-link :to="{name:'beforwebHome'}"></router-link>
      <router-link :to="{name:'our'}"></router-link>
      <router-link :to="{name:'APP'}">APP</router-link>
      <!-- <a href="#">APP</a> -->
      <a href="#"></a>
      <router-link :to="{name:'login'}" href="#"></router-link>
    </nav>
    <section class="ali"><router-link :to="{name:'alilogin'}"></router-link></section>
  </header>

</template>
<script>
  import {moveSocrller} from '../../assets/JS/common.js'  //
    export default {
      name:'header',
      mounted: function(){
        
      },
      methods: {
        product: function () {
          if(this.$route.path == '/'){
            moveSocrller($(".surface ").offset().top-100);  //
          }else{
            this.$router.go('/beforwebHome');
          }
        }
      }
    }
</script>
<style></style>



